<template>
    <div class="header">
      <div class="header-left">
        <div class="iconfont back-ion">&#xe624;</div>
      </div>
      <div class="header-input">
        <span class="iconfont arrow-ion">&#xe632;</span>
                     输入城市/景点/游玩主题
      </div>
      <router-link to="/ctiy">
        <div class="header-right">
                     {{this.city}}
        <span class="iconfont arrow-ion">&#xe64a;</span>
      </div>
      </router-link>
    </div>
</template>

<script>
import { mapState } from 'Vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped >
 @import '~styles/varibles.styl';
  .header
    display: flex
    line-height: $headerhight
    background: $bgColor
    color: #fff
    .header-left
      width: .64rem
      float: left
      .back-ion
        text-align: center
        font-size: .4rem
    .header-input
      flex: 1
      height: .64rem
      line-height: .64rem
      margin-top: .12rem
      margin-left: .2rem
      padding-left: .2rem
      background: #fff
      border-radius: .1rem
      color: #CCC
    .header-right
      min-width: 1.04rem
      padding: 0 .1rem;
      float: right
      text-align: center
      color: #fff
      .arrow-ion
        margin-left: -.04rem
        font-size: .24rem
</style>



