<template>
  <div>
    <ctiy-header></ctiy-header>
    <ctiy-search :cities="cities"></ctiy-search>
    <ctiy-list
      :cities="cities"
      :hot="hotCities"
      :letter="letter"
    >
    </ctiy-list>
    <ctiy-alphabet
      :cities="cities"
      @change="handlechange"
    >
    </ctiy-alphabet>
  </div>
</template>

<script>
import axios from 'axios'
import CtiyHeader from './compenents/Header'
import CtiySearch from './compenents/Search'
import CtiyList from './compenents/list'
import CtiyAlphabet from './compenents/Alphabet'
export default {
  name: 'Ctiy',
  components: {
    CtiyHeader,
    CtiySearch,
    CtiyList,
    CtiyAlphabet
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get('/api/city.json')
        .then(this.handleGetCityInfoSucc)
    },
    handleGetCityInfoSucc (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    },
    handlechange (letter) {
      this.letter = letter
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>

<style lang="stylus" scoped>
</style>



