<template>
  <div class="list"  ref="wrapper">
    <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper" >
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
        <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div class="button-wrapper"
           v-for="item of hot"
           :key="item.id"
           @click="hearderClick(item.name)"
           >
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
        <div class="area"
          v-for="(item,key) of cities"
          :key="key"
          :ref="key"
        >
        <div class="title border-topbottom">{{key}}</div>
        <div class="item-list">
          <div class="item border-topbottom"
          v-for = "innerItem of item"
          :key="innerItem.id"
          @click="hearderClick(innerItem.name)"
          >{{innerItem.name}}</div>
        </div>
        </div>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import { mapState, mapMutations } from 'Vuex'
export default {
  name: 'CtiyList',
  props: {
    hot: Array,
    cities: Object,
    letter: String
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  methods: {
    hearderClick (city) {
      this.changCity(city)
      this.$router.push('/')
    },
    ...mapMutations(['changCity'])
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper, {
      click: true
    })
  },
  watch: {
    letter () {
      if (this.letter) {
        const element = this.$refs[this.letter][0]
        this.scroll.scrollToElement(element)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl';
  .border-topbottom
    &:before
      border-color: #ccc;
    &:after
       border-color: #ccc;
   .border-bottom
    &:before
      border-color: #ccc;
  .list
    position: absolute;
    overflow: hidden;
    top: 1.58rem;
    left: 0;
    bottom: 0;
    right: 0;
    .title
      line-height: .54rem;
      background: #eee;
      padding-left: .2rem;
      color: #666;
      font-size: .26rem;
    .button-list
      overflow: hidden;
      padding: .1rem .6rem .1rem .1rem
      .button-wrapper
        float: left;
        width: 33.333%;
        .button
          margin: .1rem;
          padding: .1rem 0;
          text-align: center;
          border: .02rem solid #ccc;
          border-radius: .06rem
    .item-list
      .item
        line-height: .76rem;
        padding-left: .2rem;
</style>



